import Swiper from "./Swiper";
export default {
    name: "Main",
  components: {Swiper},
  mounted() {
    let canvas = document.getElementById('virtual-tour-play');
    if (canvas.getContext){
      let ctx = canvas.getContext('2d');
      ctx.beginPath();
      ctx.fillStyle = "#FFFFFF";
      ctx.strokeStyle = '#FFFFFF';
      ctx.moveTo(270,270);
      ctx.lineTo(270,10);
      ctx.lineTo(40,135);
      ctx.fillText("( прогуляться )",150,140);
      ctx.closePath();
      ctx.stroke();
    }
  }
}
